import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import moment from 'moment';
import { EmbeddedNoteShow } from '../../components/EmbeddedNote';
import { getAlias } from '../../db/person';
import { PersonReferenceViewer } from '../../components/ReferenceViewer';
import { useQueryState } from '../../lib/UseQueryState';

export const ShowPersonPage: React.FC<{id: string}> = (props) => {
    const {data} = useData();
    const person = data.people.find(x => x.id === props.id);

    if (!person) {
        return <div>Person not found.</div>
    }

    return (
        <>
            <ToolSection>
                <NavLink to={`/people`} className="button">Back</NavLink>
                <NavLink to={`/people/${person.id}/edit`} className="button">Edit</NavLink>
            </ToolSection>
            <div className="panel">
                <div className="content" style={{padding: '1rem'}}>
                    <h1>{person.name} (@{getAlias(person)})</h1>
                    {person.birthday && <div>Birthday: {moment(person.birthday).format('YYYY-MM-DD')}</div>}
                    <EmbeddedNoteShow targetEntity={person} {...props} />
                    <PersonReferenceViewer alias={getAlias(person)} />
                </div>
            </div>
        </>
    );
}; 

export const ShowVirtualPersonPage: React.SFC<{}> = (props) => {
    const [source] = useQueryState(null, 'source');

    const urlParams = new URLSearchParams(window.location.search);
    const alias = urlParams.get('alias');

    if(!alias) {
        return <div>Person not found.</div>
    }

    return (
        <>
            <ToolSection>
                <NavLink to={source ? decodeURIComponent(source) : '/'} className="button">Back</NavLink>
                <NavLink to={`/notes/new?alias=${alias}&source=${encodeURIComponent(`${window.location.pathname}`)}`} className="button">Create</NavLink>
            </ToolSection>
            <div className="panel">
                <div className="content" style={{padding: '1rem'}}>
                    <h1>{alias}</h1>
                    <PersonReferenceViewer alias={alias} />
                </div>
            </div>
        </>
    );
}