import * as React from 'react';
import { NavLink } from 'react-router-dom';
import * as styles from '../../layout/styles';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import { convertAliasToName, getAllVirtualPersonAliases } from '../../db/person';
import { cx } from 'emotion';

type ExistingPerson = {
    id: string;
    name: string;
    isRef: false;
};

type VirtualPerson = {
    name: string;
    alias: string;
    isRef: true;
};


export const PeoplePage: React.FC<{}> = props => {
    const {data} = useData();
    const [virtualReferences, setVirtualReferences] = React.useState<string[]>();

    React.useEffect(() => {
        setVirtualReferences(getAllVirtualPersonAliases(data.people, data.notes));
    }, [data.people, data.notes]);

    const people: ExistingPerson[] = data.people.map(p => ({ id: p.id, name: p.name, isRef: false }));
    const virtualPeople: VirtualPerson[] = virtualReferences ? virtualReferences.map(r => ({ name: convertAliasToName(r), alias: r, isRef: true })) : [];
    const allPeople: (ExistingPerson|VirtualPerson)[] = people.concat(virtualPeople as any);

    const renderTarget = (target: ExistingPerson|VirtualPerson) => {

        if (!target.isRef)
        {
            return (
                <NavLink key={target.id} to={`/people/${target.id}`} className={cx('item', styles.hoverGlow)}>
                    <div>
                        {target.name}
                    </div>
                </NavLink>
            );
        }

        return (
            <NavLink key={target.alias} to={`/people/virtual?alias=${target.alias}&source=${encodeURIComponent(`${window.location.pathname}`)}`} className={cx('item', styles.hoverGlow)} style={{color: '#aaa'}}>
                <div>
                    {target.name}
                </div>
            </NavLink>
        );
    };

    return (
        <>
            <ToolSection>
                <NavLink to={`/people/new`} className="button">New Person</NavLink>
            </ToolSection>

            <div className="list">
                {allPeople.sort((a, b) => a.name.localeCompare(b.name)).map(t => renderTarget(t))}
            </div>
        </>
    );
};
