import { Entity } from "./entity";
import { HasNote, Note } from "./note";

export interface Person extends Entity, HasNote {
    type: 'person';
    name: string;
    alias: string|null;
    birthday: string|null;
}

export const mentionRegex = /(?:^|\s)(@[^\s()[\].,!:'/"=]+)/g;

export function convertNameToAlias(name: string) {
    if(!name) {
        return null;
    }

    return name.toLowerCase().replace(/ /g, '_');
}

export function convertAliasToName(alias: string) {
    if (alias[0] === '@') {
        alias = alias.substring(1);
    }

    alias = alias[0].toUpperCase() + alias.substring(1);
    return alias.replace( /([-_]\w)/g, g => ` ${g[1].toUpperCase()}`)
}

export function getAlias(person: Person) {
    return `${person.alias || convertNameToAlias(person.name)}`;
}

export function extractPersonReferences(text: string) {
    if(!text) {
        return [];
    }

    const results = [];
    for(const match of text.matchAll(mentionRegex)) {
        results.push(match[1].substring(1))
    }

    return results;
}

export function extractAllPersonReferences(notes: Note[]) {
    const results = new Set<string>();

    for(const note of notes) {
        const references = extractPersonReferences(note.content);

        for(const reference of references) {
            results.add(reference);
        }
    }

    return results;
}

export function getAllVirtualPersonAliases(people: Person[], notes: Note[]) {
    const refs = extractAllPersonReferences(notes);

    for(const person of people) {
        refs.delete(getAlias(person))
    }

    return Array.from(refs);    
}
